<script>
export default {
  name: 'App',
  data() {
    return {
      showAll: false,
      titles: ["Software Developer", "Problem Solver", "Gamer"],
      currentTitle: "Software Developer",
      writeIndex: 1, index: 0, decreasing: true,
      projects: [
        {id: 1, name: "Match Found", link: "https://nhatquang1306.github.io/match-found", img: require('@/assets/matchfound.png'),
        description: "An e-sports tournament hosting website that lets users create and participate in tournaments, featuring a double elimination bracket system, real-time match updates, and a team system.",
        tags: ["Java", "PostgreSQL", "Spring Boot", "RESTful API", "Vue.js"]},
        {id: 2, name: "Virtual Stock", link: "https://nhatquang1306.github.io/virtual-stock/", img: require('@/assets/virtualstock.png'),
        description: "A realistic stock market game featuring live stock price updates, enabling users to create and join games, manage portfolios, and trade stocks with simulated funds. Each game includes a leaderboard to enhance competition and user engagement.",
        tags: ["Java", "PostgreSQL", "Spring Boot", "RESTful API", "Vue.js"]},
