        description: "A Pokémon team builder website developed with React that allows the creation of teams from a selection of 1010 Pokémons. The site offers an intuitive platform for team planning and strategy, where users can customize movesets, held items, abilities, and stats.",
        tags: ["HTML", "Javascript", "RESTful API", "React.js"]},
        {id: 4, name: "Tetris Game", link: "https://replit.com/@QuangHuynh7/tetris", img: require('@/assets/tetris.png'),
        description: "A simple retro Tetris game with collision detection, block manipulation, and scoring system, offering a nostalgic gaming experience.",
        tags: ["Java", "JFrame"]},
        {id: 5, name: "Tenmo", link: "https://github.com/nhatquang1306/money-transfer",
        description: "A Venmo-mocked program that manages transfers between users and keeps track of balances and past transfers.",
        tags: ["Java", "PostgreSQL", "Spring Boot", "RESTful API"]},
        {id: 6, name: "Vending Machine Program", link: "https://github.com/nhatquang1306/vending-machine",
        description: "A command-line vending machine program that accepts user inputs, track inventory, log sales, and refund in coins.",
        tags: ["Object-Oriented Programming", "Java"]}
      ],
      workExperiences: [
        {id: 1, company: "Tech Elevator", role: "Java Development Bootcamp Student", time: "Mar 2023 - Jun 2023",
        description: "Attended a full-stack Java coding bootcamp to learn how to develop dynamic web-based software systems."},
        {id: 2, company: "Amazon", role:"Warehouse Associate", time: "Apr 2021 - Mar 2023, Sep 2023 - Present",
        description: "Contributed to an efficient warehouse operation by accurately picking and packing orders."}
      ]
    }
  },
  created() {
    document.addEventListener("mousemove", (event) => {
      let cursor = document.querySelector(".cursor");
      cursor.style.left = event.clientX + window.scrollX + "px";
      cursor.style.top = event.clientY + window.scrollY + "px";
    })
    setInterval(() => {
      if (this.currentTitle.length == 0 || (this.currentTitle.length == this.titles[this.index].length && !this.decreasing)) {
        if (this.decreasing) {
          this.index = this.index == this.titles.length - 1 ? 0 : this.index + 1;
        } else {
          this.writeIndex = 1;
        }
        this.decreasing = !this.decreasing
      }
      if (this.decreasing) {
        this.currentTitle = this.currentTitle.slice(0, -1);
      } else {
        this.currentTitle = this.titles[this.index].substring(0, this.writeIndex);
        this.writeIndex++;
      }
    }, 500)
  },

  computed: {
    smallScreen() {
      return window.innerWidth <= 950;
    },
    filteredProjects() {
      return this.showAll ? this.projects : this.projects.slice(0, 3);
    }
  },
  methods: {
    redirect(link) {
      window.open(link, "_blank");
    },
    scroll(section) {
      let projects = document.querySelector("." + section);
      projects.scrollIntoView({behavior: 'smooth'})
    }
  }
}
</script>

